<template>
  <div class="live">
    <Stage3D 
    :resizeObj="resizeObj" 
    :mousemoveObj="mousemoveObj"
    :gltf="gltf"></Stage3D>
  </div>
</template>

<script>
import prismic from "../prismic.js";

import Stage3D from "./Stage3D.vue";
export default {
  name: 'HelloWorld',
  props: {
    mousemoveObj: {
      type: Object,
      default: () => {}
    },
    resizeObj: {
      type: Event,
      default: () => {}
    }
  },
  components: {
    Stage3D
  },
  data() {
    return {
      gltf: null
    }
  },
  methods: {

  },
  mounted() {

  },
  beforeMount() {
    // in case someone tries accessing via link and they are offline
    if (!window.navigator.onLine) window.location.href = "./index.html";
    prismic.getData().then((assets) => {
      this.gltf = assets.stage_gltf.url;
    })

  },
}
</script>

